.container {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    /* background: linear-gradient(to right, #9900ff, #cc80ff);  */
    /* background-image: url('../images/xyz.png');  */
    /* background-color: #000; */
    display: flex;
    justify-content: center;
    background-size: cover;
    align-items: center;
    background-repeat: no-repeat;
  }
  
  .innerBox {
    min-width: 480px;
    height: fit-content;
    width: fit-content;
    background-color: #fff;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    padding: 30px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
  
  .footer {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .footer .error {
    font-weight: bold;
    font-size: 0.875rem;
    color: #ff3300;
    text-align: center;
  }
  
  .footer button {
    outline: none;
    border: none;
    /* background-color: #9900ff;  */
    background-color: #000;
    color: #fff;
    border-radius: 5px;
    font-weight: bold;
    font-size: 1rem;
    padding: 10px 16px;
    width: 100%;
    transition: 100ms;
    cursor: pointer;
  }
  
  .footer button:disabled {
    background-color: gray !important;
  }
  
  .footer button:hover {
    background-color: #aa2aff;
  }
  
  .footer p {
    font-weight: 700;
    color: #000;
  }
  
  .footer p span a {
    font-weight: bold;
    color: #9900ff; 
    letter-spacing: 1px;
    font-size: 1rem;
    text-decoration: none;
  } 

/* Login.module.css */
.imageCol {
  padding: 0; /* Remove any padding from the column */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Hide any overflow */
}

.imageContainer {
  width: 100%; /* Ensure the container takes up the full width */
  height: 100%; /* Take up entire height of the column */
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginImage {
  max-width: 100%; /* Ensure the image doesn't exceed the container's width */
  max-height: 100%; /* Ensure the image doesn't exceed the container's height */
  width: 1000px;
  height: 400px;
}
